.service {
  text-align: center;
  padding-top: 90px;
  padding-bottom: 63px;
  &__container {
    @include adaptiveValue(padding-left, 103, 15, 0);
    @include adaptiveValue(padding-right, 103, 15, 0);
  }
  &__header {
    margin-bottom: 60px;
  }
  &__grid {
    grid-template-columns: repeat(3, 1fr);
    display: grid;
    gap: 78px 130px;
    margin-bottom: 45px;
  }
}

.item-service {
  display: flex;
  flex-direction: column;
  align-items: center;
  &__img {
    margin-bottom: 40px;
    // width: 293px;
    // height: 166px;
    width: 100%;
    max-width: 100%;
    overflow: hidden;
    border-radius: 39px;
    @media (max-width: 1342px) {
      width: 293px;
      max-width: 100%;
    }
    img {
      max-width: 100%;
      width: 100%;
    }
  }
}
