.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 40px 0;
  transition: .3s ease;
  z-index: 10;
  &--sticky {
    padding: 20px 0;
    background: #fff;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);

    .header__logo img {
      max-height: 150px;
    }
    .header__nav {
      margin-bottom: 21px;
    }
    .header__link {
      padding-top: 20px;
      padding-bottom: 20px;
    }
  }

  &__burger {
    display: none;
    transition: .3s all ease;
    &--active {
      transform: rotate(90deg);

    }
  }
  &__info {
    transition: .3s all ease;
    z-index: 5;
  }

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__logo {
    margin-left: -53px;
    img {
      transition: .3s all ease;
      max-height: 279px;
    }
  }
  &__nav {
    margin-bottom: 33px;
    ul {
      display: flex;
      align-items: center;
      overflow: hidden;
      border-radius: 39px;
      box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    }
    li {
      &:first-child .header__link {
        padding-left: 71px;
      }
      &:last-child .header__link {
        padding-right: 91px;
      }
    }
  }
  &__link {
    font-weight: 600;
    font-size: 18px;
    line-height: 21px;
    color: $lightcolor;
    padding: 35px 45px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: $red;
    transition: .3s ease;
    @include hover {
      background-color: #e78889;
    }
  }
  &__phones {
    display: flex;
    justify-content: flex-end;
  }
  &__phone {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    margin-right: 65px;
    font-weight: 800;
    font-size: 18px;
    line-height: 22px;
    padding: 14px 25px;
    border-radius: 29px;
    color: $lightcolor;
    background-color: $orange;
    transition: .3s ease;
    @include hover {
      background-color: #eca74b;
    }
  }
}
