.footer {
  background: $blue;
  padding: 25px 0 25px;
  &.main {
    padding-top: 20px;
    padding-bottom: 108px;
  }
  &__socials {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__item {
    margin: 0 44px;
  }
  &__link {
    img {
      transition: .3s all ease;
    }
    &:hover img {
      transform: scale(1.1);
    }
  }
}
