html {
  box-sizing: border-box;
}

body {
  font-size: $fontsize;
  line-height: $lineheight;
  color: $textcolor;
  min-width: $minWidth;
  position: relative;
  overflow-x: hidden;

  &._lock {
    overflow-y: hidden;
  }
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
  padding-top: 390px;
  background-image: url('../img/header/bg-blue.svg');
  background-position: left top;
  background-size: auto 100%;
  background-size: cover;
  background-repeat: no-repeat;
  &.white {
    background-color: $lightcolor;
    background-image: url('../img/header/bg-white.svg');
  }
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

ul {
  padding: 0;
  margin: 0;
}

label, p {
  margin-bottom: 0;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

h2 {
  font-weight: 800;
  // font-size: 40px;
  // line-height: 48px;
  @include adaptiveFont(40, 24);
  @include adaptiveValue(line-height, 48, 28, 1);
  color: $blue;
  text-align: center;
  margin-bottom: 30px;
  &.strong {
    text-transform: uppercase;
    color: $orange;
  }
}

h3 {
  font-weight: 700;
  font-size: 32px;
  line-height: 38px;
  color: #EC3636;
  text-transform: uppercase;
  text-align: center;
}

p {
  font-size: 14px;
  line-height: 17px;
  color: #000;
}

.site-container {
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.noselect {
	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-khtml-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}
