.posts {
  padding-bottom: 130px;
  @include adaptiveValue(padding-left, 42, 15, 0);
  @include adaptiveValue(padding-right, 42, 15, 0);
  &__container {
    @include adaptiveValue(padding-left, 126, 10, 0);
    @include adaptiveValue(padding-right, 126, 10, 0);
    padding-top: 31px;
    padding-bottom: 36px;
    background: #f1efef;
    border-radius: 49px;
  }
  &__header {
    margin-bottom: 40px;
  }
  &__grid {
    grid-template-columns: repeat(4, 1fr);
    display: grid;
    gap: 68px
  }
}

.item-posts {
  &__img {
    margin-bottom: 23px;
    img {
      width: 100%;
    }
  }
  &__header {
    margin-bottom: 9px;
    font-weight: 700;
    font-size: 20px;
    line-height: 24px;
    color: #000;
    text-transform: uppercase;
    text-align: center;
  }
  &__descr {
    text-align: center;
    font-size: 14px;
    line-height: 16px;
  }
}
