// button
.btn {
  border: none;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding: 33px 44px;
  border-radius: 20px;
  background: $red;
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  color: $lightcolor;
  transition: .3s all ease;

  @media (min-width: 992px) {
    &:hover {
      background: $redDark
    }
  }


  &._fw {
    width: 100%;
  }
}
