.advantages {
  padding-top: 87px;
  @include adaptiveValue(padding-left, 120, 15, 0);
  @include adaptiveValue(padding-right, 120, 15, 0);
  margin-bottom: 100px;
  &__header {
    margin-bottom: 35px;
  }
  &__container {
    background: #F1EFEF;
    border-radius: 47px;
    padding: 104px 77px 48px;
  }
  &__descr {
    font-size: 24px;
    line-height: 29px;
    margin-bottom: 80px;
  }
  &__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 74px;
  }
}

.item-advantage {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  &__icon {
    margin-bottom: 50px;
    display: flex;
    align-items: flex-start;
    justify-content: center;
    height: 120px;
    img {
      max-width: 100%;
    }
  }
  &__header {
    font-weight: 700;
    font-size: 20px;
    line-height: 24px;
    color: #000;
    text-align: center;
    width: 100%;
    text-transform: none;
  }
  &__descr {
    text-align: center;
  }
}
