.main-slider {
  &__item {
    filter: drop-shadow(0px -4px 4px rgba(0, 0, 0, 0.25));
  }
  &__pagination {
    margin-top: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.swiper-slide {
  width: 992px;
  max-width: 100vw;
}

.swiper-pagination-bullet {
  width: 14px;
  height: 14px;
  background: #60A5AC;
  opacity: 1;
  margin: 0 6px;
  &-active {
    background: $red;
  }
}
