:root {
  --font-family: Inter, "Open Sans", sans-serif;
  --content-width: 1080px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --light-color: #fff;
}

/* stylelint-disable */ /* stylelint-disable */ /* stylelint-disable */
.custom-checkbox__field:checked + .custom-checkbox__content::after {
  opacity: 1;
}

.custom-checkbox__field:focus + .custom-checkbox__content::before {
  outline: 2px solid #f00;
  outline-offset: 2px;
}

.custom-checkbox__field:disabled + .custom-checkbox__content {
  opacity: 0.4;
  pointer-events: none;
}

/* stylelint-disable */ /* stylelint-disable */
@font-face {
  font-family: "Inter";
  src: url("../fonts/../fonts/Inter-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Inter";
  src: url("../fonts/../fonts/Inter-Medium.woff2") format("woff2");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Inter";
  src: url("../fonts/../fonts/Inter-SemiBold.woff2") format("woff2");
  font-weight: 600;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Inter";
  src: url("../fonts/../fonts/Inter-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Inter";
  src: url("../fonts/../fonts/Inter-ExtraBold.woff2") format("woff2");
  font-weight: 800;
  font-display: swap;
  font-style: normal;
}
html {
  box-sizing: border-box;
}

body {
  font-size: 24px;
  line-height: 1.65;
  color: #000;
  min-width: 320px;
  position: relative;
  overflow-x: hidden;
}
body._lock {
  overflow-y: hidden;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
  padding-top: 390px;
  background-image: url("../img/header/bg-blue.svg");
  background-position: left top;
  background-size: auto 100%;
  background-size: cover;
  background-repeat: no-repeat;
}
.page__body.white {
  background-color: #fff9f9;
  background-image: url("../img/header/bg-white.svg");
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

ul {
  padding: 0;
  margin: 0;
}

label, p {
  margin-bottom: 0;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

h2 {
  font-weight: 800;
  font-size: calc(24px + 16 * ((100vw - 320px) / 1060));
  line-height: 48px;
  color: #60A5AC;
  text-align: center;
  margin-bottom: 30px;
}
@media (max-width: 1170px) {
  h2 {
    line-height: calc(28px + 20 * ((100vw - 320px) / 850));
  }
}
h2.strong {
  text-transform: uppercase;
  color: #FFB351;
}

h3 {
  font-weight: 700;
  font-size: 32px;
  line-height: 38px;
  color: #EC3636;
  text-transform: uppercase;
  text-align: center;
}

p {
  font-size: 14px;
  line-height: 17px;
  color: #000;
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.noselect {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.btn {
  border: none;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding: 33px 44px;
  border-radius: 20px;
  background: #F69294;
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  color: #fff9f9;
  transition: 0.3s all ease;
}
@media (min-width: 992px) {
  .btn:hover {
    background: #e78889;
  }
}
.btn._fw {
  width: 100%;
}

input[type=text],
input[type=email],
input[type=tel],
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.input {
  border-radius: 10px;
  width: 100%;
  display: block;
  padding: 0px 7px;
  font-size: 20px;
  line-height: 24px;
  color: #60A5AC;
  font-weight: 700;
  border: 1px solid #000;
  height: 40px;
  margin-bottom: 42px;
}
.input._error {
  border: 2px solid red;
}

textarea.input {
  resize: none;
  padding: 0px 0px;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 40px 0;
  transition: 0.3s ease;
  z-index: 10;
}
.header--sticky {
  padding: 20px 0;
  background: #fff;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}
.header--sticky .header__logo img {
  max-height: 150px;
}
.header--sticky .header__nav {
  margin-bottom: 21px;
}
.header--sticky .header__link {
  padding-top: 20px;
  padding-bottom: 20px;
}
.header__burger {
  display: none;
  transition: 0.3s all ease;
}
.header__burger--active {
  transform: rotate(90deg);
}
.header__info {
  transition: 0.3s all ease;
  z-index: 5;
}
.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__logo {
  margin-left: -53px;
}
.header__logo img {
  transition: 0.3s all ease;
  max-height: 279px;
}
.header__nav {
  margin-bottom: 33px;
}
.header__nav ul {
  display: flex;
  align-items: center;
  overflow: hidden;
  border-radius: 39px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}
.header__nav li:first-child .header__link {
  padding-left: 71px;
}
.header__nav li:last-child .header__link {
  padding-right: 91px;
}
.header__link {
  font-weight: 600;
  font-size: 18px;
  line-height: 21px;
  color: #fff9f9;
  padding: 35px 45px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #F69294;
  transition: 0.3s ease;
}
@media (any-hover: hover) {
  .header__link:hover {
    background-color: #e78889;
  }
}
.header__phones {
  display: flex;
  justify-content: flex-end;
}
.header__phone {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  margin-right: 65px;
  font-weight: 800;
  font-size: 18px;
  line-height: 22px;
  padding: 14px 25px;
  border-radius: 29px;
  color: #fff9f9;
  background-color: #FFB351;
  transition: 0.3s ease;
}
@media (any-hover: hover) {
  .header__phone:hover {
    background-color: #eca74b;
  }
}

.main-slider__item {
  filter: drop-shadow(0px -4px 4px rgba(0, 0, 0, 0.25));
}
.main-slider__pagination {
  margin-top: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.swiper-slide {
  width: 992px;
  max-width: 100vw;
}

.swiper-pagination-bullet {
  width: 14px;
  height: 14px;
  background: #60A5AC;
  opacity: 1;
  margin: 0 6px;
}
.swiper-pagination-bullet-active {
  background: #F69294;
}

.service {
  text-align: center;
  padding-top: 90px;
  padding-bottom: 63px;
}
.service__container {
  padding-left: calc(15px + 88 * ((100vw - 320px) / 1060));
  padding-right: calc(15px + 88 * ((100vw - 320px) / 1060));
}
.service__header {
  margin-bottom: 60px;
}
.service__grid {
  grid-template-columns: repeat(3, 1fr);
  display: grid;
  gap: 78px 130px;
  margin-bottom: 45px;
}

.item-service {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.item-service__img {
  margin-bottom: 40px;
  width: 100%;
  max-width: 100%;
  overflow: hidden;
  border-radius: 39px;
}
@media (max-width: 1342px) {
  .item-service__img {
    width: 293px;
    max-width: 100%;
  }
}
.item-service__img img {
  max-width: 100%;
  width: 100%;
}

.posts {
  padding-bottom: 130px;
  padding-left: calc(15px + 27 * ((100vw - 320px) / 1060));
  padding-right: calc(15px + 27 * ((100vw - 320px) / 1060));
}
.posts__container {
  padding-left: calc(10px + 116 * ((100vw - 320px) / 1060));
  padding-right: calc(10px + 116 * ((100vw - 320px) / 1060));
  padding-top: 31px;
  padding-bottom: 36px;
  background: #f1efef;
  border-radius: 49px;
}
.posts__header {
  margin-bottom: 40px;
}
.posts__grid {
  grid-template-columns: repeat(4, 1fr);
  display: grid;
  gap: 68px;
}

.item-posts__img {
  margin-bottom: 23px;
}
.item-posts__img img {
  width: 100%;
}
.item-posts__header {
  margin-bottom: 9px;
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  color: #000;
  text-transform: uppercase;
  text-align: center;
}
.item-posts__descr {
  text-align: center;
  font-size: 14px;
  line-height: 16px;
}

.footer {
  background: #60A5AC;
  padding: 25px 0 25px;
}
.footer.main {
  padding-top: 20px;
  padding-bottom: 108px;
}
.footer__socials {
  display: flex;
  align-items: center;
  justify-content: center;
}
.footer__item {
  margin: 0 44px;
}
.footer__link img {
  transition: 0.3s all ease;
}
.footer__link:hover img {
  transform: scale(1.1);
}

.advantages {
  padding-top: 87px;
  padding-left: calc(15px + 105 * ((100vw - 320px) / 1060));
  padding-right: calc(15px + 105 * ((100vw - 320px) / 1060));
  margin-bottom: 100px;
}
.advantages__header {
  margin-bottom: 35px;
}
.advantages__container {
  background: #F1EFEF;
  border-radius: 47px;
  padding: 104px 77px 48px;
}
.advantages__descr {
  font-size: 24px;
  line-height: 29px;
  margin-bottom: 80px;
}
.advantages__grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 74px;
}

.item-advantage {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
.item-advantage__icon {
  margin-bottom: 50px;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  height: 120px;
}
.item-advantage__icon img {
  max-width: 100%;
}
.item-advantage__header {
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  color: #000;
  text-align: center;
  width: 100%;
  text-transform: none;
}
.item-advantage__descr {
  text-align: center;
}

.consultation__container {
  text-align: center;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
  max-width: 986px;
  padding-top: 93px;
  padding-bottom: 166px;
}
.consultation__header {
  margin-bottom: 60px;
}
.consultation__btn {
  margin-top: 12px;
  width: 100%;
  max-width: 302px;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

@media (max-width: 1316px) {
  .service__grid {
    grid-template-columns: repeat(2, 1fr);
  }
  .item-service__img {
    width: 100%;
  }
  .posts__grid {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 1252px) {
  .header__logo {
    margin-left: 0;
  }
  .header__logo img {
    max-height: 200px;
  }
  .header__link {
    padding: 30px 20px;
  }
  .header__nav {
    margin-bottom: 20px;
  }
  .header__phone {
    margin-right: 40px;
  }
  .header__phone:last-child {
    margin-right: 0;
  }
}
@media (max-width: 1104px) {
  .advantages__container {
    padding-top: 70px;
  }
  .advantages__grid {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 974px) {
  .page__body {
    padding-top: 135px;
    background-image: none;
  }
  .header {
    background: #fff;
    padding: 10px 0;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  }
  .header__logo img {
    max-width: 100px;
  }
  .header__burger {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    width: 60px;
    height: 50px;
  }
  .header__burger span, .header__burger::after, .header__burger::before {
    width: 100%;
    height: 10px;
    border-radius: 20px;
    background: #FFB351;
  }
  .header__burger::after, .header__burger::before {
    content: "";
    display: block;
  }
  .header__info {
    position: absolute;
    width: 100%;
    top: 135px;
    background: #fff;
    right: 0;
    transform: translateX(100%);
    height: calc(100vh - 135px);
    overflow-y: auto;
    padding: 30px 15px 40px;
  }
  .header__info--active {
    transform: translateX(0);
  }
  .header__nav ul {
    flex-direction: column;
    box-shadow: none;
    align-items: flex-end;
    overflow: visible;
  }
  .header__link {
    padding: 15px 0 !important;
    font-size: 24px;
    line-height: 28px;
    background: 0;
    color: #000;
  }
  .header__phones {
    flex-direction: column;
  }
  .header__phone {
    margin: 0 10px 20px;
  }
  .header__phone:last-child {
    margin-right: 10px;
    margin-bottom: 0;
  }
}
@media (max-width: 794px) {
  .service__grid {
    grid-template-columns: repeat(1, 1fr);
  }
  .posts__grid {
    grid-template-columns: repeat(1, 1fr);
  }
  .item-service__img {
    max-width: 400px;
  }
  .item-posts__img {
    max-width: 400px;
    margin: 0 auto 23px;
  }
  .advantages__grid {
    grid-template-columns: repeat(1, 1fr);
  }
  .advantages__descr {
    font-size: 20px;
  }
  .item-advantage__icon {
    margin-bottom: 35px;
  }
  .item-advantage__descr {
    max-width: 400px;
    margin: 10px auto 0;
  }
  .advantages__container {
    padding: 40px 40px 60px;
  }
  .consultation__container {
    padding: 40px 15px 80px;
  }
  .main-title-block {
    padding: 60px 15px 40px;
  }
}
@media (max-width: 512px) {
  .footer {
    padding: 25px 0;
  }
  .footer.main {
    padding-bottom: 40px;
  }
  .footer__socials {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
  .footer__item {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 10px 0;
  }
}
.main-title-block {
  padding: 80px 15px 60px;
  background: #60A5AC;
}
.main-title-block__header {
  text-align: center;
  text-transform: uppercase;
  font-weight: 600;
  font-size: calc(22px + 10 * ((100vw - 320px) / 1060));
  line-height: calc(30px + 8 * ((100vw - 320px) / 1060));
  color: #fff;
}