@media (max-width: 1316px) {
  .service__grid {
    grid-template-columns: repeat(2, 1fr);
  }
  .item-service__img {
    width: 100%;
  }
  .posts__grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 1252px) {
  .header__logo {
    margin-left: 0;
    img {
      max-height: 200px;
    }
  }
  .header__link {
    padding: 30px 20px;
  }
  .header__nav {
    margin-bottom: 20px;
  }
  .header__phone {
    margin-right: 40px;
    &:last-child {
      margin-right: 0;
    }
  }
}

@media (max-width: 1104px) {
  .advantages__container {
    padding-top: 70px;
  }
  .advantages__grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 974px) {
  .page__body {
    padding-top: 135px;
    background-image: none;
  }
  .header {
    background: #fff;
    padding: 10px 0;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    &__logo img {
      max-width: 100px;
    }
    &__burger {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
      width: 60px;
      height: 50px;
      span, &::after, &::before {
        width: 100%;
        height: 10px;
        border-radius: 20px;
        background: $orange;
      }
      &::after, &::before {
        content: '';
        display: block;
      }
    }
    &__info {
      position: absolute;
      width: 100%;
      top: 135px;
      background: #fff;
      right: 0;
      transform:translateX(100%);
      height: calc(100vh - 135px);
      overflow-y: auto;
      padding: 30px 15px 40px;
      &--active {
        transform: translateX(0);
      }
    }
    &__nav {
      ul {
        flex-direction: column;
        box-shadow: none;
        align-items: flex-end;
        overflow: visible;
      }
    }
    &__link {
      padding: 15px 0 !important;
      font-size: 24px;
      line-height: 28px;
      background: 0;
      color: #000;
    }
    &__phones {
      flex-direction: column;
    }
    &__phone {
      margin: 0 10px 20px;
      &:last-child {
        margin-right: 10px;
        margin-bottom: 0;
      }
    }
  }
}

@media (max-width: 794px) {
  .service__grid {
    grid-template-columns: repeat(1, 1fr);
  }
  .posts__grid {
    grid-template-columns: repeat(1, 1fr);
  }
  .item-service__img {
    max-width: 400px;
  }
  .item-posts__img {
    max-width: 400px;
    margin: 0 auto 23px;
  }
  .advantages__grid {
    grid-template-columns: repeat(1, 1fr);
  }
  .advantages__descr {
    font-size: 20px;
  }
  .item-advantage__icon {
    margin-bottom: 35px;
  }
  .item-advantage__descr {
    max-width: 400px;
    margin: 10px auto 0;
  }
  .advantages__container {
    padding: 40px 40px 60px;
  }
  .consultation__container {
    padding: 40px 15px 80px;
  }
  .main-title-block {
    padding: 60px 15px 40px;
  }
}

@media (max-width: 512px) {
  .footer {
    padding: 25px 0;
    &.main {
      padding-bottom: 40px;
    }
  }
  .footer__socials {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
  .footer__item {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 10px 0
  }
}
