// input
input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.input {
  border-radius: 10px;
  width: 100%;
  display: block;
  padding: 0px 7px;
  font-size: 20px;
  line-height: 24px;
  color: $blue;
  font-weight: 700;
  border: 1px solid #000;
  height: 40px;
  margin-bottom: 42px;

  &._focus {}

  &._error {
    border: 2px solid red;
  }
}

textarea.input {
  resize: none;
  padding: 0px 0px;
}

